@import '../../../base';

.multi-drop {
  .selected-container {
    //padding-bottom: 10px;
  
    .mag-container {
      margin-right: 22px;
      max-height: 32px;
      display: flex;
  
      .mag-icon {
        align-items: center;
        filter: $color-filter-body;
      }
    }
  
    .selected-arrow {
      display: flex;
      // margin-top: -15px;
  
      img {
        justify-content: center;
      }
    }
  
    .selected-arrow:hover {
      filter: $color-filter-hover;
    }
  
    .selected-arrow:active {
      filter: $color-filter-body;
    }
  
    .selected-items-container {
      width: 100%;
      // margin-top: -10px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
  
      .selected-text-multi {
        height: 36px;
        border-radius: 200px;
        // padding: 0 20px;
        // margin: 7px;
        display: inline-block;
        width: auto;
        overflow: inherit;
        flex-grow: 1;
        padding-top: 5px;
      }
  
      .selected-item-container {
        height: 36px;
        border-radius: 200px;
        padding: 0 20px;
        margin: 7px;
        background-color: $color-line;
        display:inline-block;
  
        .selected-item {
          display:flex;
          height: 100%;
          
          p {
            text-align: center;
            align-items: center;
            justify-content: center;
            margin: auto;
            color: $color-title;
            font-weight: $font-weight-semibold;
          }
  
          .selected-item-destroy {
            background: none;
            color: inherit;
            border: none;
            padding: 0;
            font: inherit;
            cursor: pointer;
            outline: inherit;
  
            img {
              margin-left: 20px;
              filter: $color-filter-body;
            }
          }
  
          .selected-item-destroy:hover {
            filter: $color-filter-hover;
          }
        }
      }
    }
  }
}
